import React from 'react'

export const About = () => {
  let myStyle = {
    width: "30rem",
    margin: "25px 50px"
  }
  return (
    <div className="card " style={myStyle}>
      <div className="card-body">
        <h5 className="card-title">About the app</h5>
        <p className="card-text">This To Do App is developed using React.js by Abhishek Raj. <br/> For more details</p>
        <a href="https://github.com/iamraj45" target="_blank" rel="noopener noreferrer" className="card-link btn btn-primary">Click here</a>
      </div>
    </div>
  )
}
